import axios from 'axios';
axios.defaults.baseURL = 'https://pixabay.com';
const KEY_USER = '22553588-f2f977a115121553e22566e7d';

export const fetch = async (picTitle, page) => {
  const {data} = await axios.get(
    `/api/?q=${picTitle}&page=${page}&key=${KEY_USER }&image_type=photo&orientation=horizontal&per_page=12`,
  );
  return data.hits;
};
